<template>
    <div
        class="InfoCard"
        :class="computedClass"
    >
        <header @click="click">
            <h1>{{title}}</h1>
            <div class="arrow-button">
                <img src="@/assets/images/arrow-down.svg">
            </div>
        </header>
        <SlideUpDown :active="opened" class="content">
            <slot></slot>
        </SlideUpDown>
    </div>
</template>

<script>
    import SlideUpDown from 'vue-slide-up-down';

    export default {
        name: 'InfoCard',
        components: {
            SlideUpDown,
        },
        props: {
            title: {
                type: String,
                required: true,
            },
            opened: {
                type: Boolean,
                default: false,
            },
        },
        computed: {
            computedClass() {
                return {
                    'opened': this.opened,
                };
            },
        },
        methods: {
            click() {
                this.$emit('click');
            },
        },
    };
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/_variables.scss';

    .InfoCard {
        user-select: none;
        color: white;
        font-weight: normal;

        header {
            display: flex;
            justify-content: space-between;
            cursor: pointer;

            h1 {
                font-size: 1.3em;
                font-weight: normal;
                color: $primary;
                margin: 0;
                transition: 0.1s ease-out color;
            }

            .arrow-button {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 30px;
                height: 30px;
                transform: scale(1, 1);
                transition: 0.2s ease-out transform;

                img {
                    flex: 1 1;
                    width: 40px;
                }
            }

            &:hover {
                h1 {
                    color: lighten($primary, 20);
                }
            }

            &:active {
                h1 {
                    color: lighten($primary, 50);
                    transition: 0s ease-out transform;
                }
            }
        }

        &.opened {
            .arrow-button {
                transform: scale(1, -1);
            }
        }
    }
</style>
